.preloader {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader__logo {
  position: relative;
}

.preloader__logo:before {
  content: "";
  background-color: #030303;
  width: 100%;
  height: 100%;
  animation: 3s ease-in-out both loading;
  position: absolute;
  top: 0;
  right: 0;
}

@keyframes loading {
  from {
    right: 0;
  }

  12% {
    right: -26px;
  }

  24% {
    right: -60px;
  }

  36% {
    right: -80px;
  }

  48% {
    right: -100px;
  }

  52% {
    right: -120px;
  }

  64% {
    right: -140px;
  }

  80% {
    right: -160px;
  }

  100% {
    right: -100%;
  }
}

.header {
  z-index: 2;
  background-color: #030303;
  border-bottom: 1px solid #101317;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  height: 66px;
  display: flex;
}

.header__logo {
  display: none !important;
}

@media (width >= 768px) {
  .header__logo {
    display: flex !important;
  }
}

.header__logo-small {
  color: #0ff;
  font-size: 34px;
  font-weight: 700;
