}

.header__auth {
  gap: .5333rem;
}

.nav {
  box-sizing: border-box;
  background: #030303;
  border-top: 1px solid #101317;
  justify-content: space-between;
  width: 100%;
  height: 66px;
  padding-inline: 20px;
  display: flex;
  position: fixed;
  bottom: 0;
  right: 0;
