.banner {
  background: url("banner-4.b0daf487.jpg") center / cover no-repeat;
  border-top-left-radius: 1.625rem;
  border-top-right-radius: 1.625rem;
  height: 300px;
  margin: 8px 20px;
}

@media (width >= 768px) {
  .banner {
    background: url("banner-2.bfb03b97.jpg") center / cover no-repeat;
  }
}

@media (width >= 1200px) {
  .banner {
    background: url("banner-1.d2f7a66f.jpg") center / cover no-repeat;
  }
}

.banner__container {
  height: 100%;
}

.banner__title {
  margin: 0;
  font-size: 1.4667rem;
  font-weight: 700;
  line-height: 3.3333rem;
}

.banner__content {
  color: #fff;
  flex-direction: column;
  justify-content: center;
