  width: 50%;
  height: 100%;
  padding: 20px;
  display: flex;
}

.btn {
  box-sizing: border-box;
  color: #000;
  white-space: nowrap;
  background-color: #0ff;
  border-radius: .5333rem;
  padding: .6rem;
  font-weight: 700;
  transition: opacity .3s;
}

.btn-red {
  color: #fff;
  background-color: #cf0e48;
  animation: 1.5s ease-in-out infinite both heartbeat;
}

.btn:hover {
  opacity: .6;
  animation: none;
}

@keyframes heartbeat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(.91);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(.98);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(.87);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

